.contact-section{
    position: relative;
}
.contact-page{
    position: absolute;
    top: 17vh;
    left: 2.3vw;
    background-color: black;
    min-height: 64vh;
    max-width: 94vw;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-family: var(--font-ubuntu);
}
.contact-section h1{
    color: #c7c7c7;
    font-size: 48px;
    font-weight: bold;
    position: absolute;
    left: 7vw;
    bottom: -10vh;
}
.contact-section h2{
    color: #c7c7c7;
    margin-bottom: 3vh;
    font-weight: 400;
    font-size: 22px;
    margin-top: 0;
}
.contact-page-inner{
    width: 100%;
    padding:6vh 6vw;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
.contact-page-right{
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact-page-left{
    margin-bottom:2vh ;
    width: 35vw;
}
.contact-page-right img{
    width: 27vw;
}
.contact-email-form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 415px;
}
.contact-email-entities{
    width: 100%;
    margin: 1vh 0 1vh 0;
}
.email-btn-box{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact-email-form input,textarea{
    font-size: 0.9rem;
    box-sizing: border-box;
    width: 100%;
    background-color: black;
    font-family: var(--font-ubuntu);
    color: #c7c7c7;
    padding: 1.5vh;
    border-radius: 6px;
    border: 2px solid grey;
}
::placeholder{
    color:white;
}
.contact-section form{
    max-width: 100%;
}
.contact-email-form span{
    font-size: 1rem;
    position: relative;
    z-index: 1;
}
.email-submit-btn{
    font-family: var(--font-ubuntu);
    outline: none;
    font-size: 18px;
    background: transparent;
    border: 1.5px solid var(--neon);
    color: var(--neon);
    padding: 6px 20px;
    height: 5.5vh;
    border-radius: 2px;
    /* width: 7vw; */
    width: 90px;
    cursor: pointer;
    position: relative;
    transition: 0.2s color ease-in-out;
    display: flex;
    justify-content: center;
    align-items: center;
}
.email-submit-btn::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0%;
    background: var(--neon);
    z-index: 0;
    transition: 0.2s width ease-in-out;
}
.email-submit-btn:hover::before{
    width: 100%;
}
.email-submit-btn:hover{
    color: black;
}
.email-submit-btn-sent{
    font-family: var(--font-ubuntu);
    outline: none;
    font-size: 18px;
    background: transparent;
    border: 1.5px solid #9a9a9a;
    color: #9a9a9a;
    padding: 6px 20px;
    height: 5.5vh;
    width: 90px;
    border-radius: 2px;
    position: relative;
    transition: 0.2s color ease-in-out;
    display: flex;
    justify-content: center;
    align-items: center;
}