@import url('https://fonts.googleapis.com/css2?family=Acme&family=Poppins:wght@400;500;600;700&family=Roboto+Slab:wght@300;400;500;600;700&family=Ubuntu:wght@300;400;500;700&display=swap');
body {
  margin: 0;
  font-family: var(--font-acme);
  background-color: black;
  color: white;
}
html, body {
  max-width: 100%;
  /* overflow-x: hidden; */
}
html{
  scroll-behavior: smooth;
}
.flex{
  display: flex;
}
.flex-center{
  display: flex;
  justify-content: center;
  align-items: center;
}
.flex-between{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

a{
  text-decoration: none;
  color: inherit;
}

:root{
  --font-ubuntu: 'Ubuntu', sans-serif;
  --font-poppins: 'Poppins', sans-serif;
  --font-roboto: 'Roboto Slab', serif;
  --font-acme: 'Acme', sans-serif;
  --light-black:#121317;
  --neon:#18f08b;
  /* hsl(316, 30%, 36%) #5BE9B9 #18f08b #83b633 */
  /*#121317 #222629 */
}
.slide-btn::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background: var(--neon);
  z-index: -1;
  transition: 0.2s width ease-in-out;
}
.slide-btn:hover::before{
  width: 100%;
}
