.underline-btn::before,
.underline-btn::after{
  content: '';
  position: absolute;
  height: 2px;
  width: 0;
  /* background-color: var(--neon); */
  background-color: #18f08b;
  transition: 0.2s ease-in-out;
}
.underline-btn::before{
  right: 0;
  top: -4px;
}
.underline-btn::after{
  left: 0;
  bottom: -5px;
  transform-origin: left;
}
.underline-btn:hover::before{
  width:100%;
  transform-origin: right;
}
.underline-btn:hover::after{
  width:100%;
  transform-origin: right;
}
